import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Suburban(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/suburban.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[8.83, 1.31, 3.71]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere162.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere162_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere162_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Suburban;
useGLTF.preload("/glossarySelected/suburban.glb");
